<template>
  <div class="home">
    <div class="mian-box">
      <div class="form-box">
        <el-form :inline="true" class="demo-form-inline">
          <el-col class="line" :span="6" :offset="4">
            <el-form-item label="商品名称:">
              <el-input placeholder="输入商品名称"></el-input>
            </el-form-item>
          </el-col>
          <el-form-item label="商品分组">
            <el-select placeholder="全部" value>
              <el-option label="家具日用" value="shai"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品类型">
            <el-select placeholder="全部" v-model="cates">
              <el-option
                :label="item.cate_zh"
                :value="item.cate"
                v-for="item in cate"
                :key="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <div class="f-bottom">
            <el-col class="line sales" :span="4" :offset="4">
              <el-form-item label="销量:">
                <el-input></el-input>
                <span>-</span>
                <el-input></el-input>
              </el-form-item>
            </el-col>
            <el-col class="line sales" :span="4">
              <el-form-item label="价格:">
                <el-input></el-input>
                <span>-</span>
                <el-input></el-input>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="6">
              <el-form-item label="库存扣减方式:">
                <el-select placeholder="全部" value>
                  <el-option label=">1000" value="ahai"></el-option>
                  <el-option label=">100" value="dfj"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-form-item>
              <el-button type="primary">查询</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>

      <div class="table">
        <el-tabs type="border-card">
          <el-tab-pane label="全部">
            <div class="tab-box">
              <div style="margin-top: 20px">
                <el-checkbox>当页全选</el-checkbox>
                <!-- v-model="checked" -->
                <el-button >下架</el-button>
                <el-button >删除</el-button>
                <el-button >批量删除</el-button>
                <el-button >取消选择</el-button>
              </div>
              <!-- 表格 -->
              <el-table 
                class="tab-1" 
                :data="Data" 
                max-height="400"
                border
              >
                <el-table-column 
                  type="selection" 
                  width="55">
                </el-table-column>
                <el-table-column 
                  fixed prop="name" 
                  label="商品名" width="250">
                </el-table-column>
                <el-table-column 
                  fixed prop="img" 
                  label="图片" width="120"
                >
                  <template slot-scope="scope">
                    <img :src="scope.row.img" :alt="scope.row.name" class="img-box" />
                  </template>
                </el-table-column>
                <el-table-column 
                  prop="price" 
                  label="价格（元）" 
                  width="100">
                </el-table-column>
                <el-table-column 
                  prop="cate" 
                  label="品类" 
                  width="120">
                </el-table-column>
                <el-table-column 
                  prop="city" 
                  label="是否推荐" 
                  width="100">
                </el-table-column>
                <el-table-column 
                  fixed="right" 
                  label="操作" 
                  width="200"
                >
                  <template slot-scope="scope">
                    <el-button  type="text" size="small">编辑</el-button>
                    <el-button @click="handleClick(scope.row._id)" type="text"  size="small">删除</el-button>
                    <el-button type="text" size="small">推广</el-button>
                    <el-button type="text" size="small">复制</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
          <el-tab-pane label="销售中">销售中</el-tab-pane>
          <el-tab-pane label="销售中">已售馨</el-tab-pane>
          <el-tab-pane label="销售中">定时任务补偿</el-tab-pane>
          <!-- 分页 -->
        <div class="pagination">
          <el-pagination
            background
            @current-change='change'
            :current-page='page'
            layout="prev, pager, next, jumper"
            :total="1000"
            :page-size='10'>
            > 
          </el-pagination>
        </div>
        </el-tabs>
        
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      page:1,
      Data: [],
      cate:[],
      cates:''
    };
  },
  mounted(){
    this.getHotGoodList();
    //获取品类
    this.$http.getAllCates().then(res=>{
      res.unshift({cate_zh:'全部'})
      this.cate=res.data
      
    })
    
  },
  watch:{
    cates: function (){
      this. getHotGoodList()
    }
  },
  methods:{
    // 获取商品
    getHotGoodList(){
      let params={
        hot:false,
        // page:this.page,
        // size:10,
        // cate:this.cates
      }
      this.$http.getHotGoodList(params).then(res=>{
        console.log('res',res)
        this.Data=res.data
      })
    }, 
    // 页码切换
    change(page){
      this.page=page
      this.getHotGoodList()
    },
    // 删除
    handleClick(id){
      console.log("id",id)
      let params={
        id:id
      }
      this.$http.delGood(params).then(res=>{
        console.log(res)
       this.getHotGoodList();
       confirm("确定删除吗？")
      })
    },
  }
};
</script>

<style lang='scss' scoped>
.home {
  margin: 0;
  padding: 0;
}

.tab-box {
  margin: 10px auto;
  .el-table {
    width: 100%;
  }
}
.el-row {
  .el-col {
    width: 200px;
  }
}
.sales {
  .el-input {
    width: 80px;
  }
}
.img-box {
  display: block;
  width: 100px;
  height: 100px;
}
.table{
  margin: 0 auto;
  max-width: 1000px;
}
.pagination{
  position: relative;
  left: 400px;
}
</style>